/* src/components/HeroSection/HeroSection.css */
.hero-section {
    position: relative;
    overflow: hidden;
    height: 100vh; /* <-- ¡Asegúrate de que esto esté aquí! */
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #FFD700;
    padding-top: 50px;
    box-sizing: border-box;
    /* ... otros estilos ... */
    
  }
  
  .hero-section h1 {
    font-size: 3rem;
    margin-bottom: 20px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
  }
  
  .hero-section p {
    font-size: 1.2rem;
    margin-bottom: 30px;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.7);
  }
  
  .hero-section a {
      color: #050505;
  }
  .hero-section a:hover {
      color: #000000;
  }

  .hero-avatar {
  width: 150px; /* Tamaño del avatar */
  height: 150px;
  border-radius: 50%; /* Lo hace circular */
  object-fit: cover;
  border: 4px solid var(--color-primary-light);
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.5);
  margin-bottom: 20px;
  transition: transform 0.3s ease-in-out;
}

.hero-avatar:hover {
  transform: scale(1.05);
}